
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&family=Poppins:wght@400;600&display=swap');
.App {
  font-family: 'Poppins', sans-serif;
  text-align: center;
  padding: 20px;
  background: linear-gradient(90deg, rgb(171, 4, 46) 0%, rgb(30, 3, 77) 100%); 
  min-height: 100vh;
  color: white;
}

.container {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  gap: 20px;
}

.form-container {
  width: 30%; 
  background-color: rgba(0, 0, 0, 0.4);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0);
}

h1, h3 {
  color: white;
  font-weight: bold;
}

.graphs-container {
  width: 65%; 
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
  max-height: 80vh;
}

button {
  margin: 10px 1;
  padding: 10px;
  background-color: #010101;
  color: rgba(160, 160, 160,1);
  border: none;
  cursor: pointer;
  border-radius: 5px;
  font-size: 1rem;
}

button:hover {
  background-color: #3b82f6;
  color: white;
}

input {
  padding: 10px;
  width: 85%;
  border-radius: 15px;
  background-color: #353333;
  color: white;
}

input:focus {
  outline: none;
  border-color: #000000;
}

label {
  font-weight: bold;
  margin-bottom: 5px;
  display: block;
  color: #ff00e1;
}

h3 {
  font-size: 1.4rem;
  margin-bottom: 20px;
}

.graph-item {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
  }

  .form-container, .graphs-container {
    width: 100%;
  }
}
